<template lang="html">
  <mt-checklist v-model="currValue" :options="quarter"></mt-checklist>
</template>

<script>
export default {
  props: {
    quarter: {
      type: Array,
      default () {
        return []
      }
    },
    value: {
      type: Array,
      default () {
        return []
      }
    }
  },
  watch: {
    value: {
      handler (val) {
        this.currValue = val
      },
      deep: true
    },
    currValue (val) {
      this.$emit('input', val)
    }
  },
  data () {
    return {
      currValue: this.value
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.mint-cell {
  min-height: .88rem;
  background: transparent !important;
}
.mint-checklist .mint-cell {
  margin: 0 .22rem;
}
.mint-checklist-title {
  margin: 0;
}
.mint-cell-wrapper {
  padding: 0;
  border-bottom: 1px solid @lineColor;
}
.mint-checkbox-input:checked + .mint-checkbox-core {
  background-color: @checkColor;
  border-color: @checkColor;
}
.mint-checklist-label {
  padding: 0;
}
label.mint-checklist-label {
  margin-left: .1rem;
  display: inline-block;
}
.mint-cell:last-child {
  background-repeat: inherit;
}
</style>



