<template>
  <login-box>
    <default-head header="登录">
      <a slot="head-right" @click="$router.push('/register')">注册</a>
    </default-head>
    <!-- 表单 -->
    <form action="#" class="form-group">
      <div class="form-group-item">
        <label for="phone" class="form-group-item-text">账号</label>
        <div class="form-group-item-ctn">
          <input v-model="loginInfo.mobile" v-validate="'required'" type="telephone" id="phone"
          class="form-group-item-input" name="phone" placeholder="手机号/会员编号">
        </div>
        <!-- <div v-show="errors.has('phone')" class="">{{errors.first('phone')}}</div> -->
      </div>

      <div class="form-group-item">
        <label for="possword" class="form-group-item-text">密码</label>
        <div class="form-group-item-ctn">
          <input v-model="loginInfo.password" v-validate="'required'" type="password"
          id="password" class="form-group-item-input" name="password" placeholder="请输入密码">
          <i class="cha-icon" @click="clearPassword"></i>
        </div>
      </div>

      <div class="check-box">
        <label for="remember">
          <img :src="checkImg" class="check-box-reset" @click="changeCheckImg()">
          <input type="checkbox" name="" id="remember" v-model="checked">
          <span class="check-box-text" @click="changeCheckImg()">记住账号</span>
        </label>
      </div>

      <div class="login-button">
        <input type="button" @click.prevent="loginSubmit()" value="登录">
        <span class="forget activeColor" @click="$router.push('/forgetpossword')">忘记密码?</span>
      </div>
    </form>

  </login-box>
</template>

<script>
import api from '@/api'
import base64 from 'base-64'
import utf8 from 'utf8'
import minutils from '@/utils/minUtils'
import loginBox from '@/components/login/loginBox'
import defaultHead from '@/components/head/defaultHead'
import checkList from '@/components/minCommon/checkList'
import checkon from '@/assets/img/login/checkon@2x.png'
import checkout from '@/assets/img/login/checkout@2x.png'

export default {
  components: { loginBox, defaultHead, checkList },
  data () {
    return {
      checkImg: checkout,
      checked: false,
      loginInfo: { mobile: localStorage.getItem('curMobile') && base64.decode(localStorage.getItem('curMobile')) || '', password: '' }  // 登录对象
    }
  },
  computed: {
    curMobile: function () {
      return localStorage.getItem('curMobile') && base64.decode(localStorage.getItem('curMobile')) || ''
    }
  },
  methods: {
    changeCheckImg () {
      this.checkImg = this.checkImg === checkout ? checkon : checkout
    },
    clearPassword (e) {
      this.loginInfo.password = ''
      e.srcElement.previousElementSibling.focus()
    },
    loginSubmit (sign) {
      this.$validator.validateAll().then(() => {
        this.ajaxLogin(this.loginInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    setStore (user) {
      // 勾选记住账号 先utf8 后base64
      if (this.checked) {
        localStorage.setItem('curMobile', user.user.mobile && base64.encode(user.user.mobile))
        sessionStorage.setItem('curUser', user && base64.encode(utf8.encode(JSON.stringify(user))))
      } else {
        // 未勾选记住账号 登录其他账号后清除账号
        // if (this.curMobile !== base64.encode(this.loginInfo.mobile)) {
        //   localStorage.removeItem('curMobile')
        // }
        sessionStorage.setItem('curUser', user && base64.encode(utf8.encode(JSON.stringify(user))))
      }
      // 后期优化路由拦截
      if (this.$route.query.from === 'appraise') {
        this.$router.push('/appraiseorder')
      } else {
        this.$router.push('/personcenter')
      }
    },
    ajaxLogin (data) {
      api.login(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.setStore(result.data) // 1表示个人登录
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  @import '../assets/css/variable.less';

  .login {
    padding-top: .88rem;
  }

  .form-group {
    width: 100%;
    .form-group-item {
      display: flex;
      width: 100%;
      height: .88rem;
      line-height: .88rem;
      font-size: .32rem;
      padding: 0 .22rem;
      background: #fff;
      border-bottom: 1px solid @lineColor;
      .form-group-item-text {
        width: 1.34rem;
        color: @defaultColor;
      }
      .form-group-item-ctn {
        flex: 1;
        position: relative;
        height: .8rem;
        .cha-icon {
          position: absolute;
          top: 50%;
          right: 0;
          transform: translateY(-50%);
          width: .44rem;
          height: .66rem;
          background: url(../assets/img/login/cha@2x.png) no-repeat center center;
          background-size: .22rem .22rem;
        }
        .form-group-item-input {
          width: 100%;
          height: 100%;
          border: none;
          font-size: .32rem;
          color: @defaultColor;
          &::placeholder {
            color: @lightColor;
          }
        }
        .error {
          position: absolute;
          right: 0;
          top: 0;
          color: @activeColor;
        }
      }
    }
    .check-box {
      height: .88rem;
      font-size: .3rem;
      padding: .29rem 0;
      label {
        .check-box-reset {
          display: inline-block;
          position: relative;
          left: .17rem;
          top: -.02rem;
          vertical-align: middle;
          width: .44rem;
          height: .44rem;
        }
        input {
           visibility: hidden;
        }
        .check-box-text {
          display: inline-block;
          text-indent: -.2rem;
        }
      }
    }
    .login-button {
      width: 100%;
      height: .98rem;
      position: relative;
      padding: 0 .22rem;
      margin-top: 0.6rem;
      .item {
        float: left;
        width: 50%;
        padding: 0 .18rem;
        height: .86rem;
      }
      input {
        width: 100%;
        height: 100%;
        border: 0;
        border-radius: .05rem;
        font-size: .34rem;
        background: @activeColor;
        color: @whiteColor;
      }
      .forget {
        position: absolute;
        font-size: .3rem;
        text-decoration: underline;
        right: .22rem;
        top: 1.28rem;
      }
    }
  }

</style>



